.todoItem {
  padding: 13px;
  margin-bottom: -1px;
  display: flex;
  justify-content: space-between;
  background: #ffffff;
  border: 1px solid #ccc;
  &:first-child {
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
  }
  &:last-child {
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
  }
  &Done {
    &>span {
      text-decoration: line-through;
    }
  }
  &Important {
    color: green;
    font-weight: bold;
  }
  &Del {
    // border: 1px solid red;
    background: #337ab7;
  }
}
.todoSubmit–°lose {
  margin-right: 5px;
  background: #5cb85c;
  color: #ffffff;
  border: none;
  border-radius: 3px;
  font-size: 12px;
  line-height: 1.5;
  cursor: pointer;
}
.todoSubmitOk {
  color: #ffffff;
  background: #d9534f;
  border: none;
  border-radius: 3px;
  font-size: 12px;
  line-height: 1.5;
  cursor: pointer;
}