.searchPanel {
    display: flex;
}
.todoTitle {
    margin: 0;
    padding-right: 19px;
    font-size: 36px;
}
.todoSearch {
    display: flex;
}
.todoInput {
    width: 295px;
    margin-right: 7px;
    padding: 7px;
    border-radius: 4px;
    border: 1px solid #ccc;
}
.todoSubmit {
    padding: 8px 13px;
    background: #337ab7;
    color: #ffffff;
    border: none;
    border-radius: 4px;
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    &:hover {
      background: #4798de;
    }
}