.formBlock {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.buttonSeveBlock {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 20px;
}
.buttonSeve {
  padding: 8px 13px;
  background: #337ab7;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  font-size: 17px;
  cursor: pointer;
  &:hover {
    background: #4798de;
  }
}
